import '@carbon/charts/styles.css';
import 'zingchart/es6';
import 'zingchart/modules-es6/zingchart-chord.min.js';
import 'zingchart/modules-es6/zingchart-depth.min.js';

import BarChart from '@/components/BarChart.vue';
import DonutChart from '@/components/DonutChart.vue';
import GroupedBarChart from '@/components/GroupedBarChart.vue';
import LinesChart from '@/components/LinesChart.vue';
import StackedBarChart from '@/components/StackedBarChart.vue';
import Vs from 'd3-vs';
import Vue from 'vue';
import VueCookie from 'vue-cookie';
import zingchartVue from 'zingchart-vue';

import App from './App.vue';
import vuetify from './plugins/vuetify';
import router from './router';

// import chartsVue from '@carbon/charts-vue';
// Tell Vue to use the plugin

// IBM Plex should either be imported in your project by using Carbon
// or consumed manually through an import

// Vue.use(chartsVue);
Vue.use(VueCookie);

// install globally all components
Vue.use(Vs);

Vue.config.productionTip = false;
// install globally to app
Vue.component("zingchart", zingchartVue);

Vue.component("DonutChart", DonutChart);
Vue.component("LinesChart", LinesChart);
Vue.component("BarChart", BarChart);
Vue.component("StackedBarChart", StackedBarChart);
Vue.component("GroupedBarChart", GroupedBarChart);
new Vue({
  router,
  vuetify,
  render: (h) => h(App),
}).$mount("#app");
