<template>
  <v-container>
    <v-row justify="center">
      <v-col class="text-center text-h3" v-if="loading" xl="10">
        Загрузка...
      </v-col>
      <v-col v-if="!loading" md="8">
        <v-card class="mb-6 mt-6" elevation="0">
          <v-card-title class="justify-center text-center text-h4 mb-4">
            Сервисы
          </v-card-title>
          <v-row justify="center">
            <MyBarChart
              v-if="servicesChart"
              :items="servicesChart.items"
              :legend="servicesChart.legend"
            ></MyBarChart>
          </v-row>
        </v-card>
        <v-card
          :key="selectedService"
          v-if="selectedService && visitsChart"
          class="mb-6 mt-6"
          elevation="0"
        >
          <v-card-title class="justify-center text-center text-h4 mb-4">
            Динамика визитов
          </v-card-title>
          <v-row justify="center">
            <v-col md="4"
              ><v-select
                :items="serviceWithStatistic"
                v-model="selectedService"
                item-value="name"
                item-text="name"
                hint="Выберите сервис"
                persistent-hint
                @change="onServiceChange"
              ></v-select
            ></v-col>
          </v-row>
          <v-row justify="center" style="height: 700px">
            <zingchart height="100%" :data="visitsChart"></zingchart>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";
import MyBarChart from "@/components/MyBarChart.vue";
export default {
  name: "Home",
  components: {
    MyBarChart
  },
  data() {
    return {
      servicesChart: null,
      loading: true,
      visitsChart: null,
      selectedService: null
    };
  },
  async created() {
    const res = await axios("./data.json");
    this.sourceData = res.data.sort((a1, a2) => a2.conversion - a1.conversion);
    this.servicesChart = this.getServicesChart(this.sourceData);
    this.selectedService = this.serviceWithStatistic[0].name;
    this.visitsChart = this.getVisitsChart(this.serviceWithStatistic[0]);
    this.loading = false;
  },
  computed: {
    serviceWithStatistic() {
      return this.sourceData.filter(e => e.statistic && e.statistic.length);
    }
  },
  methods: {
    onServiceChange(name) {
      const service = this.serviceWithStatistic.find(e => e.name === name);
      this.selectedService = service.name;
      this.visitsChart = null;
      this.$nextTick(() => {
        this.visitsChart = this.getVisitsChart(service);
      });
    },
    getVisitsChart(data) {
      return {
        type: "line",
        legend: {
          align: "center",
          backgroundColor: "none",
          borderWidth: "0px",
          item: {
            cursor: "hand"
          },
          marker: {
            type: "circle",
            borderWidth: "0px",
            cursor: "hand"
          },
          verticalAlign: "top"
        },
        plot: {
          aspect: "spline",
          lineWidth: "2px",
          marker: {
            borderWidth: "0px",
            size: "5px"
          }
        },
        plotarea: {
          margin: "dynamic 70"
        },
        tooltip: {
          borderRadius: "3px",
          borderWidth: "0px"
        },
        preview: {
          adjustLayout: true
        },
        scaleY: {
          guide: {
            lineStyle: "dashed"
          },
          item: {
            fontColor: "#E3E3E5"
          },
          lineColor: "#E3E3E5",
          minorGuide: {
            alpha: 0.7,
            lineColor: "#E3E3E5",
            lineStyle: "dashed",
            lineWidth: "1px",
            visible: true
          },
          minorTick: {
            lineColor: "#E3E3E5"
          },
          minorTicks: 1,
          tick: {
            lineColor: "#E3E3E5"
          }
        },
        crosshairX: {
          marker: {
            alpha: 0.5,
            size: "7px"
          },
          plotLabel: {
            borderRadius: "3px",
            multiple: true
          },
          scaleLabel: {
            backgroundColor: "#53535e",
            borderRadius: "3px"
          }
        },
        scaleX: {
          zooming: true
        },
        tooltip: {
          borderRadius: "3px",
          borderWidth: "0px"
        },
        preview: {
          adjustLayout: true
        },
        series: [
          {
            text: data.name,
            values: data.statistic.map(e => [e.date, e.value])
          }
        ]
      };
    },
    getServicesChart(data) {
      return {
        items: data.map(e => ({
          title: e.name,
          link: e.link,
          values:
            e.conversion != null || e.visitors !== null
              ? [e.conversion, e.visitors]
              : null
        })),
        legend: [
          {
            title: "Переходы",
            color: "#32dcac"
          },
          {
            title: "Посетители",
            color: "#505aea"
          }
        ]
      };
    }
  }
};
</script>
