<template>
  <div class="bar-chart">
    <div class="bar-legend" v-if="legend && legend.length">
      <div class="legend-item" :key="item.title" v-for="item of legend">
        <div
          class="legend-icon"
          :style="{
            background: item.color
          }"
        ></div>
        {{ item.title }}
      </div>
    </div>
    <div class="bar-lines">
      <div class="bar-row" v-for="item of items" :key="item.title">
        <div class="bar-left">
          <div class="bar-title">
            {{ item.title }}
            <template v-if="item.values">
              <strong :style="{ color: legend[0].color }"
                >{{ `${item.values[0]}` }}
              </strong>
              /
              <strong :style="{ color: legend[1].color }">{{
                `${item.values[1]}`
              }}</strong>
            </template>
            <strong v-else>Нет данных</strong>
            <br /><a
              class="link-icon-href"
              v-if="item.link"
              :href="item.link"
              target="_blank"
              >{{ item.link }}
              <v-icon class="link-icon">mdi-open-in-new</v-icon>
            </a>
          </div>
        </div>
        <div class="bar-right" v-if="item.values">
          <template v-if="stacked">
            <div class="bar-line">
              <template v-for="(subitem, i) of legend">
                <v-tooltip v-if="item.values[i]" :key="subitem.title" bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <div
                      v-bind="attrs"
                      v-on="on"
                      class="bar-line-item"
                      :title="subitem.title"
                      :style="{
                        background: subitem.color,
                        width: `${getPercent(item.values[i] || 0)}%`
                      }"
                    ></div>
                  </template>
                  <span>{{ subitem.title }}: {{ item.values[i] }} </span>
                </v-tooltip>
              </template>
            </div>
          </template>
          <template v-if="!stacked">
            <div
              v-for="(subitem, i) of legend"
              :key="subitem.title"
              class="bar-line"
            >
              <v-tooltip v-if="item.values[i]" bottom>
                <template v-slot:activator="{ on, attrs }">
                  <div
                    v-bind="attrs"
                    v-on="on"
                    class="bar-line-item"
                    :title="subitem.title"
                    :style="{
                      background: subitem.color,
                      width: `${getPercent(item.values[i] || 0)}%`
                    }"
                  ></div>
                </template>
                <span>{{ subitem.title }}: {{ item.values[i] }} </span>
              </v-tooltip>
            </div>
          </template>
        </div>
        <div class="bar-right" v-else>
          <span class="error--text">
            Нет данных
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    legend: {
      type: Array,
      default() {
        return [];
      }
    },
    items: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data() {
    return {
      stacked: false
    };
  },
  computed: {
    maxValue() {
      return this.items.reduce((sum, curr) => {
        const valuesMax = this.stacked
          ? (curr.values || []).reduce((b, a) => a + b, 0)
          : (curr.values || []).reduce((b, a) => (a > b ? a : b), 0);
        return sum > valuesMax ? sum : valuesMax;
      }, 0);
    }
  },
  methods: {
    getPercent(val) {
      return this.maxValue ? (100 * val) / this.maxValue : 0;
    },
    getSum(item) {
      return (item.values || []).reduce((b, a) => a + b, 0);
    }
  }
};
</script>
