<template>
  <ccv-pie-chart :data="chartData" :options="options"></ccv-pie-chart>
</template>
<script>
export default {
  name: "Chart",
  components: {},
  props: ["data", "title", "leftTitle", "bottomTitle", "height"],
  computed: {
    chartData() {
      return this.data || [];
    },
    options() {
      return {
        title: this.title,
        legend: false,
        pie: {
          alignment: "center",
        },

        height: this.height || "500px",
      };
    },
  },
};
</script>
  