<template>
  <ccv-stacked-bar-chart
    :data="chartData"
    :options="options"
  ></ccv-stacked-bar-chart>
</template>
<script>
export default {
  name: "Chart",
  components: {},
  props: ["data", "title", "leftTitle", "bottomTitle", "height"],
  computed: {
    chartData() {
      return this.data || [];
    },
    options() {
      return {
        title: this.title,
        axes: {
          left: {
            title: this.bottomTitle,
            mapsTo: "key",
            scaleType: "labels",
          },
          bottom: {
            mapsTo: "value",
            title: this.leftTitle,
            scaleType: "linear",
            stacked: true,
          },
        },
        resizable: true,

        curve: "curveMonotoneX",
        height: this.height || "500px",
      };
    },
  },
};
</script>
  